import React, { useState, useEffect, useCallback } from 'react';
import cls from './BeginModule.module.scss';
import { connect } from 'react-redux';
import { first, take } from 'rxjs/operators';
import { TheAccordionSelectContext } from '../../../context/beginProcess.context';
import { processTaskAction, useProcessTaskContext } from '../../../context/processTask.context';
import { getProcessById } from '../../../services/processes.service';
import { useBeforeUnload } from 'react-use';
import { store } from '../../../reduxStore/store';
import { setCart, setMessageBar, setProcessModule, setShowMenu } from '../../../reduxStore/actions/flowActions';
import ModuleStep from '../ModulesComp/ModuleStep';
import { Button, ModalR, PermissionWrapper, Spinner, po } from '../../common';
import { isMobile } from 'react-device-detect';
import { importModuleComponentsFromExcelFile, pinModule, unpinModule } from '../../../services/module.service';
import { getComponents } from '../../../services/components.service';
import { setAuthenticatedUser } from '../../../reduxStore/actions/authActions';
import { getAuthenticatedUser } from '../../../services/user.service';
import { sessionService } from 'redux-react-session';
import { useNavigate, useParams } from 'react-router-dom';
import { ComponentContext } from '../../../context/components.context';
import { useDropzone } from 'react-dropzone';
import { uploadIcon } from '../../../assets/images/icons';

const BeginModule = props => {
	const { begunModule, user, companyProcesses, cart } = props;
	const [navigateTo, setnavigateTo] = useState('');
	const [pending, setPending] = useState(false);
	const [loading, setLoading] = useState(false);

	const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();

	const [showImport, setShowImport] = useState(false);
	const [importing, setImporting] = useState(false);

	const { moduleID } = useParams();

	const onDrop = useCallback(
		acceptedFiles => {
			acceptedFiles.forEach(file => {
				if (['xls', 'xlsx', 'csv'].indexOf(file?.name?.toString().split('.')[1]) === -1) {
					return;
				}
				const reader = new FileReader();
				reader.onabort = () => {};
				reader.onerror = () => {};
				reader.onload = evt => {};
				reader.readAsBinaryString(file);
				reader.onloadend = _e => {
					setImporting(true);

					importModuleComponentsFromExcelFile(file, begunModule?.processModule?.module?.id).subscribe({
						next: () => {
							setImporting(false);
							getComponents(false, true).pipe(take(1)).subscribe({});
						},
						complete: () => {
							setImporting(false);
						},
						error: error => {
							setImporting(false);
						},
					});
				};
			});
		},
		[begunModule?.processModule?.module]
	);
	const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

	const navigate = useNavigate();
	useEffect(() => {
		if (begunModule?.processModule?.isUpdating === undefined) {
			let found = companyProcesses.find(process => process.id === begunModule.processModule.id);
			if (found && found.steps.length > 0) {
				const process = {
					...begunModule.processModule,
					...found,
					steps: found.steps
						.filter(step => step?.options?.showStepAll == undefined || step?.options?.showStepAll == true || (step?.options?.showStepAll == false && step?.options?.showOnly?.filter(a => user.positions.map(pos => pos.id).indexOf(a) > -1).length > 0))
						.map((step, i) => {
							return { ...step, rank: i + 1 };
						}),
				};
				store.dispatch(setProcessModule(process));
				setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: process.steps[begunModule.processModule.stepIndex ?? 0] });
			} else {
				getProcessById(begunModule.processModule.id)
					.pipe(first())
					.subscribe({
						next: data => {
							const process = {
								...begunModule.processModule,
								...data,
								steps: data.steps.filter(step => step?.options?.showStepAll == undefined || step?.options?.showStepAll == true || (step?.options?.showStepAll == false && step?.options?.showOnly?.filter(a => user.positions.map(pos => pos.id).indexOf(a) > -1).length > 0)),
							};
							store.dispatch(setProcessModule(process));
							setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: process.steps[begunModule.processModule.stepIndex ?? 0] });
						},
						error: _error => {},
					});
			}
		} else if (begunModule?.processModule?.isUpdating === true) {
			setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: begunModule.processModule.steps[begunModule.processModule.stepIndex ?? 0] });
		}
		store.dispatch(setMessageBar({ show: true, message: `Fetching all shared ${begunModule.processModule.module?.name} data...`, color: 'var(--app-color-gray-light)', spinner: true, bottom: true }));
		getComponents(true, true)
			.pipe(take(1))
			.subscribe({
				complete: () => store.dispatch(setMessageBar({ show: false, message: `` })),
			});

		if (moduleID) {
			const searchParams = new URLSearchParams(location?.search);
			setnavigateTo({ key: searchParams.get('key'), value: searchParams.get('value') });
		}
	}, []);

	const addImportArea = () => (
		<div className={cls.importArea}>
			<div {...getRootProps()}>
				<input {...getInputProps()} style={{ display: 'none', alignContent: 'center', outline: 'none' }} />
				{isDragActive ? (
					<div className={cls.importText}>
						<img alt="" src={uploadIcon} />
						<p>{'Drop the files here ...'}</p>
					</div>
				) : (
					<div className={cls.importText}>
						{!importing && <img alt="" src={uploadIcon} />}
						<p>{importing ? <Spinner size={80} type={'ThreeDots'} /> : 'Click or drag your file here to begin import. (ex .csv)'}</p>
					</div>
				)}
			</div>
		</div>
	);

	useBeforeUnload(true, 'Are you sure want to leave this ');
	return (
		<TheAccordionSelectContext>
			<ComponentContext>
				<div className={cls.headerContainer}>
					<div />
					{!isMobile && processTaskContext?.currentStep?.rank > 1 && begunModule.processModule.steps?.length > 0 && (
						<p className={cls.modalLabelHeader} style={{ padding: '15px 10px 10px 10px' }}>
							{`Step ${processTaskContext?.currentStep?.rank - 1 ?? 0} of ${begunModule.processModule.steps?.find(step => step.is_end == true)?.rank - 1 ?? begunModule.processModule.steps.length - 1}`}
						</p>
					)}
					<div style={{ display: 'flex', minWidth: '80px' }}>
						<PermissionWrapper permissions={[po.blueprints.process.create]}>
							{begunModule?.processModule?.module?.options?.allowImport === true && processTaskContext?.currentStep?.rank == 1 && <Button title={showImport ? 'Hide' : 'Import'} style={{ marginLeft: '0px', width: '80px' }} clicked={() => setShowImport(!showImport)}></Button>}
						</PermissionWrapper>

						<Button
							title={user?.pinned_modules?.filter(pm => pm.id === begunModule.processModule.module_id).length > 0 ? 'Unpin' : 'Pin'}
							loading={loading}
							style={{ marginLeft: '0px', width: '80px', display: processTaskContext?.currentStep?.rank > 1 ? 'none' : '' }}
							clicked={() => {
								setLoading(true);
								if (user?.pinned_modules?.filter(pm => pm.id === begunModule.processModule.module_id).length > 0) {
									unpinModule(begunModule.processModule.module_id)
										.pipe(first())
										.subscribe({
											next: () => {
												getAuthenticatedUser()
													.pipe(first())
													.subscribe({
														next: data => {
															sessionService.saveUser({ ...data });
															setLoading(false);
														},
													});
											},
										});
								} else {
									pinModule(begunModule.processModule.module_id)
										.pipe(first())
										.subscribe({
											next: () => {
												getAuthenticatedUser()
													.pipe(first())
													.subscribe({
														next: data => {
															sessionService.saveUser({ ...data });
															setLoading(false);
															store.dispatch(setShowMenu(true));
															navigate('/dashboard');
														},
													});
											},
										});
								}
							}}
						/>

						<Button
							title={'Close'}
							style={{ marginLeft: '5px', width: '80px', display: processTaskContext?.currentStep?.rank > 1 ? 'none' : '' }}
							clicked={() => {
								setTimeout(() => {
									store.dispatch(setShowMenu(true));
								}, 10);

								if (processTaskContext?.items?.length > 0) {
									if (begunModule.processModule.module?.options?.saveCart === true && (begunModule.processModule.isUpdating === false || begunModule.processModule.isUpdating === undefined)) {
										setPending(true);
									} else {
										navigate('/dashboard');
										store.dispatch(setProcessModule(''));
									}
								} else {
									navigate('/dashboard');
									store.dispatch(setProcessModule(''));
								}
							}}
						/>
					</div>
				</div>
				{showImport && addImportArea()}
				<ModuleStep navigateTo={navigateTo} isEditing={false} />
				<ModalR id="pending-component-modal" blured={true} scrollable={false} isOpen={pending} shadowedContainerStyle={{ width: isMobile ? '100%' : '30%' }}>
					<div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
						<p className={cls.modalLabelHeader}>{'Are you working on a transaction?'}</p>
						<div className={cls.modalLabel}>{`You can press ${cart?.items?.length > 0 ? "'Update Cart'" : "'Add to Cart'"} to save any transaction you are working on without submitting it. You can resume it by clicking the cart icon on the menu bar. ${
							cart?.items?.length > 0 ? "Pressing 'Empty Cart' will clear previous contents of your cart, and exit" : ''
						}`}</div>
						<div className={cls.modalButtons}>
							<Button
								title={'Close only'}
								style={{ width: '120px' }}
								clicked={() => {
									setTaskProcessContext({
										action: processTaskAction.clearOngoingTransaction,
									});
									window.history.back();
									store.dispatch(setProcessModule(''));
								}}
							/>
							<Button
								title={cart?.items?.length > 0 ? 'Update Cart' : 'Add to Cart'}
								light
								style={{ width: '130px' }}
								clicked={() => {
									processTaskContext.components.filter(c => c.selected_fields.filter(sf => sf.data_type_name === 'ID').forEach(field => (field.value = undefined)));
									processTaskContext.newStepComponents.filter(c => c.selected_fields.filter(sf => sf.data_type_name === 'ID').forEach(field => (field.value = undefined)));
									begunModule?.processModule?.steps?.length > 0 && begunModule?.processModule?.steps?.filter(step => step?.component_blueprints?.filter(cb => cb.selected_fields.filter(field => field.data_type_name === 'ID').filter(f1 => (f1.value = undefined))));

									const draft = JSON.parse(
										JSON.stringify({
											components: processTaskContext.components,
											newStepComponents: processTaskContext.newStepComponents,
											items: processTaskContext.items,
											begunModule: { ...begunModule.processModule, stepIndex: 1 },
										})
									);
									store.dispatch(setCart(draft));
									setTaskProcessContext({
										action: processTaskAction.clearOngoingTransaction,
									});
									window.history.back();
									store.dispatch(setProcessModule(''));
								}}
							/>
							<Button
								title={'Empty Cart'}
								light
								style={{ width: '120px', display: cart?.items?.length > 0 ? '' : 'none' }}
								clicked={() => {
									store.dispatch(setCart({}));
									setTaskProcessContext({
										action: processTaskAction.clearOngoingTransaction,
									});
									window.history.back();
									store.dispatch(setProcessModule(''));
								}}
							/>
						</div>
					</div>
				</ModalR>
			</ComponentContext>
		</TheAccordionSelectContext>
	);
};

const mapStateToProps = store => {
	const { begunModule, companyProcesses, cart } = store.flowReducer;
	const { user } = store.sessionReducer;
	return { begunModule, user, companyProcesses, cart };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetAuthenticatedUser: user => dispatch(setAuthenticatedUser(user)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(BeginModule);
